body {
  max-width: 100%;
  padding: 20px;
  padding-top: 0px;
  font-family: 'PT Sans', sans-serif;
  background: -webkit-gradient(linear, left top, right top, color-stop(50%, #282d38), color-stop(50%, #15171b));
  background: -webkit-linear-gradient(left, #282d38 50%, #15171b 50%);
  background: -o-linear-gradient(left, #282d38 50%, #15171b 50%);
  background: linear-gradient(to right, #282d38 50%, #15171b 50%);
  color: #fff;
}

h1 {
  font-size: 50px;
}



